.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.info-meta {
  color: #E08709;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    flex-direction: row;
  }
  .derecha .img-boton {
    position: relative;
    z-index: 100;
    width: 350px;
    margin: 0;
  }
  .derecha .img-boton:hover {
    cursor: pointer;
  }
}
.derecha .img-boton {
  margin: 2rem 0 2rem 0;
  max-width: 3rem;
  max-height: 3rem;
  width: 3rem;
  filter: invert(100%);
}

.inicio {
  min-height: 80rem;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}
@media (min-width: 768px) {
  .inicio {
    min-height: 70rem;
  }
}

.container-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .container-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }
}
.container-header .logo a {
  color: #FFFFFF;
}
.container-header .logo span {
  font-weight: 500;
}

.nav {
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s ease-in-out, opacity 0.3s linear, height 0.5s linear;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    visibility: visible;
    opacity: 1;
    flex-direction: row;
    text-align: center;
  }
}
.nav a {
  padding: 0 1rem;
  color: #FFFFFF;
}
.nav a:hover {
  color: #71B100;
  transition: 0.5s all;
}

.visible {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.containerg-presentacion {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
@media (min-width: 768px) {
  .containerg-presentacion {
    min-height: 70rem;
  }
}
.containerg-presentacion p {
  color: #FFFFFF;
  margin: 0;
}
.containerg-presentacion h1, .containerg-presentacion h2 {
  color: #FFFFFF;
  font-weight: 600;
  font-size: 3.8rem;
  text-align: left;
  margin: 0;
}

.presentacion-texto {
  position: absolute;
  right: 50%;
  bottom: 7%;
}

/*MENU DE HAMBURGUESA*/
@media (min-width: 768px) {
  .menu-mobile {
    display: none;
  }
}
.menu-mobile img {
  width: 3rem;
}
.menu-mobile img:hover {
  cursor: pointer;
}

.footer {
  background-color: #333333;
  padding: 2rem 0;
}
.footer p {
  text-align: center;
  color: #FFFFFF;
}
.footer .nav {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.header {
  background-color: #333333;
}

.grid-nosotros {
  text-align: center;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .grid-nosotros {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
    text-align: left;
  }
}
.grid-nosotros .icono {
  margin: 0 auto;
}
.grid-nosotros .icono img {
  height: 10rem;
  width: 100%;
}
.grid-nosotros h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .container-nosotros {
    display: grid;
    grid-template-columns: 1.3fr 1fr;
    column-gap: 3rem;
  }
}

.casas {
  padding: 3rem 0;
}

.grid-casas {
  padding: 0rem 10rem;
  display: grid;
  gap: 1fr;
  grid-template-columns: repeat(1, 1fr);
  row-gap: 3rem;
}
@media (min-width: 768px) {
  .grid-casas {
    display: grid;
    gap: 2fr;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}

.grid-casa {
  border: 2px solid #e1e1e1;
  background-color: #f0f0f0;
}
.grid-casa .imgcasa {
  margin: 0;
}
.grid-casa p {
  padding: 0 2rem;
}
.grid-casa .boton {
  display: flex;
  justify-content: center;
}
.grid-casa .boton-amarillo {
  border: none;
  text-decoration: none;
  background-color: #E08709;
  text-align: center;
  font-weight: 700;
  margin: 20px auto;
}
.grid-casa .boton-amarillo a {
  margin: 0 20px;
  display: block;
  color: #FFFFFF;
  padding: 1.2rem;
}
.grid-casa .boton-amarillo a:hover {
  background-color: #c77808;
}
.grid-casa .boton-amarillo a {
  color: #FFFFFF;
}

.precio {
  color: #71B100;
  font-weight: 800;
  font-size: 30px;
}

.boton-verde {
  margin: 40px auto;
  border: none;
  text-decoration: none;
  background-color: #71B100;
  text-align: center;
  font-weight: 700;
  width: 100%;
}
.boton-verde a {
  margin: 0 0;
  display: block;
  color: #FFFFFF;
  padding: 1.2rem;
}
.boton-verde a:hover {
  background-color: #619800;
}
.boton-verde a {
  width: 100%;
}

ul {
  list-style: none;
  max-width: 40rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
ul li {
  flex: 1;
  display: flex;
}
ul li img {
  flex: 0 0 3rem;
}

.boton-amarillo {
  border: none;
  text-decoration: none;
  background-color: #E08709;
  text-align: center;
  font-weight: 700;
}
.boton-amarillo a {
  margin: 0 0;
  display: inline-block;
  color: #FFFFFF;
  padding: 1.2rem;
}
.boton-amarillo a:hover {
  background-color: #c77808;
}

.contacto {
  position: relative;
}

.texto-contacto {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 10;
  height: 100%;
  color: #FFFFFF;
}
.texto-contacto h3 {
  font-weight: 800;
}
.texto-contacto p {
  text-align: center;
  color: #FFFFFF;
}

.container-texto {
  position: absolute;
  width: 100%;
  height: 100%;
}

.imagen-contacto img {
  max-height: 50rem;
  filter: brightness(0.6);
}

.enviar-form {
  color: #FFFFFF;
  padding: 1rem;
}
.enviar-form:hover {
  cursor: pointer;
}

.blog-testimoniales {
  padding: 2rem 0;
}

@media (min-width: 768px) {
  .containerg-bt {
    display: grid;
    grid-template-columns: 2fr 1.15fr;
    column-gap: 5rem;
  }
  .containerg-bt .blog {
    display: grid;
    grid-template-columns: 2fr 2.5fr;
    column-gap: 1rem;
    margin-bottom: 2rem;
  }
  .containerg-bt .blog h4 {
    margin: 0;
    color: #000000;
  }
  .containerg-bt .blog h4::after {
    content: "";
    border-bottom: 5px solid #E08709;
    display: block;
    width: 200px;
    margin-top: 1rem;
  }
}

.testimonio {
  background-color: #71B100;
  border-radius: 15px;
  padding: 1rem;
}
.testimonio blockquote {
  position: relative;
  font-size: 2.4rem;
  padding-left: 1rem;
  color: #FFFFFF;
}
.testimonio blockquote::before {
  position: absolute;
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  left: -4rem;
  top: -2rem;
  display: block;
}
.testimonio span {
  color: #FFFFFF;
  text-align: right;
  display: inline-block;
  width: 100%;
  font-weight: 500;
  font-size: 2rem;
}

.no-grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

.container-anuncio {
  max-width: 60rem;
}

.blog h4 {
  color: #333333;
}

.formulario legend {
  font-size: 2rem;
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0;
}
.formulario label {
  font-weight: bold;
  display: block;
  text-transform: uppercase;
}
.formulario input:not([type=submit], [type=radio]), .formulario textarea, .formulario select {
  padding: 0.5rem;
  width: 100%;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

.forma-contacto {
  display: flex;
  max-width: 30rem;
  justify-content: space-evenly;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p, .dark-mode a {
  color: #FFFFFF;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .grid-casa {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .grid-casa .precio {
  color: #71B100;
}
.dark-mode li img {
  filter: invert(100%);
}
.dark-mode li span {
  color: #FFFFFF;
  font-weight: bold;
  padding-left: 1rem;
}
.dark-mode .footer {
  background-color: #333333;
}
.dark-mode .blog a h4 {
  color: #FFFFFF;
}
.dark-mode fieldset {
  filter: invert(100%);
}